.conteiner_box {
    width: 100%;   
    text-align: center;
       position: absolute;
        top: 0px;
        left: 0px;
    background: #474545;
    box-shadow: 0px -5px 48px 26px rgba(3, 6, 8, 0.07) ;
    transition-duration: 1.5s;
}

.button_return{
    width: 50px;
    height: 50px;
    padding: 18px;
    position: relative;
    left: -100px;
    background: #0000003d;   
}


.conteiner_box h2 {
    font-family: 'Cormorant Infant';
    font-style: normal;
    font-weight: 700;

}

.conteiner_box span {
    display: block;
    margin: 30px 10px;
    line-height: 1.23;
    font-size: 2ex;
    font-style: italic;
}



